:root{

  
    --color1:  #13293D ;
    --color2:  #133c64 ;
    --color3:  #006494 ;
    --color4:  #247Ba0 ;
    --color5:  #bfc1c2 ;
    --color6:  #E8f1f2;
    
    scroll-behavior: smooth;
}

.sectionfomr{
    background-color:var(--color6) ;
    display: flex;
    flex-direction: column;
    justify-content:center;
    color:(--color5);
    padding-top: 10rem;
    
    margin: auto;
   

}



.contatotitulo{
    text-align: center;
    color: var(--color1);
    font-size:1.5rem;
    padding-bottom: -5rem;
    
}

.textarea{
    width: 20rem;
    height: 5rem;
}

.formularioo{
display: flex;
flex-direction: column;
align-items: center;
margin: auto;
width: 30%;
}

label{
padding: .5rem;
font-size: large;
}
.buttonsend{
    align-content: center;
}


.formulario{
    display: flex;
    flex-direction:row;
    color: var(--color1);
    width: 80%;
    margin: auto;
    
}

.botao{
    padding-top: 2rem;
    background-color: #13293D;
}

.contatofinal{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.emailwhats{
    display: flex;
    flex-direction: column;
    color: var(--color1);
    text-align: left;
    padding-bottom: 3rem;
    
}

.formbasic{
    font-size: larger;
}

@media(max-width: 800px) {
    .sectionfomr{
        background-color:var(--color6) ;
        display: flex;
        flex-direction: column;
        justify-content:center;
        color:(--color5);
        width: 100%;
    
    }


    .contatotitulo{
        text-align: center;
        color: var(--color1);
        font-size:1rem;
        padding: .5rem;
        
    }
    
    .textarea{
        width: 20rem;
        height: 5rem;
    }
    
    .formularioo{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    
    }
    
    label{
    padding: .5rem;
    
    }
  }
  