:root{

  
    --color1:  #13293D ;
    --color2:  #133c64 ;
    --color3:  #006494 ;
    --color4:  #247Ba0 ;
    --color5:  #bfc1c2 ;
    --color6:  #E8f1f2;
    
    scroll-behavior: smooth;
    
    @import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@1,500&display=swap');
    }
    .header{
      
      position:fixed;
      display: flex;
      justify-content: space-between;
      background-color: var(--color1);
      align-items: center;
      align-content: center;
      padding: 2rem;
      
      
      
      width: 100%;
      z-index: 20;
    }
    
    .Logo {
      width: 20rem;
    }
    
    .menu{
      display: flex;
      align-content: center;
      
    
    }
    
    .menu {
      list-style: none;
      
      
    }
    
    .menu li a{
      text-decoration: none;
      color: var(--color5);
      font-family: 'Nunito', sans-serif;
      text-transform: uppercase;
      font-size: 1rem;
    }
    
    .menu li a:hover{
     color: white;
     font-weight: bolder;
     border-bottom:#247Ba0 .3rem solid ;
     
    }
    
    @media(max-width: 800px) {
      header{
      position:fixed;
      display: flex;
      justify-content: center;

      background-color: var(--color1);
      
      align-content: center;
      width: 100%;
      
      }
      .Logo {
        width: 15rem;
        margin-right: 3.6rem;
      }
      
      .menu{
        margin-left: .3rem;
      }
      }
      
    